// rwd alias ----------------------------

@mixin tablet {
  @include mq(tablet) {
    @content; } }

@mixin desktop {
  @include mq(desktop) {
    @content; } }

@mixin landscape {
  @include mq($and: $mq-landscape) {
    @content; } }

@mixin retina {
  @include mq($and: $mq-hidpi) {
    @content; } }

// image ----------------------------

// asset-related functions from: https://github.com/assetsjs/postcss-assets

@mixin retina-image($url, $ext) {
  $image: $url + $ext;
  $size: size($image);
  width: width($image);
  height: height($image);
  background-image: resolve($image);
  @include retina {
    background-image: resolve($url + '@2x' + $ext);
    background-size: $size; } }

@mixin retina-background($url, $ext, $bg-size: cover) {
  $image: $url + $ext;
  background-image: resolve($image);
  background-size: $bg-size;
  @include retina {
    background-image: resolve($url + '@2x' + $ext);
    background-size: $bg-size; } }

// debug ----------------------------

@mixin xx($opacity: .2) {
  outline: 1px solid rgba(red, $opacity); }

// alias ----------------------------

@mixin input-placeholder-color($color) {
  &::-webkit-input-placeholder {
    color: $color;
    -webkit-font-smoothing: antialiased; }

  &:-moz-placeholder {
    color: $color; }

  &::-moz-placeholder {
    color: lighten($color, 10%); }

  &:-ms-input-placeholder {
    color: $color; } }

// layout ----------------------------

@mixin pie-clearfix {
  &:after {
    content: '';
    display: table;
    clear: both; } }

// icon sprites -----------------------

%sprite {
  $sprite: map-get($icons, sprite);
  $sprite-url: map-get($sprite, svgPath);
  display: inline-block;
  background-image: resolve($sprite-url); }

%ie-sprite {
  $sprite: map-get($icons, sprite);
  $sprite-url: map-get($sprite, pngPath);
  display: inline-block;
  background-image: resolve($sprite-url); }

@mixin icon($name, $type: all) {
  $icon-map: map-get($icons, $name);

  $width-px: map-get($icon-map, width);
  $height-px: map-get($icon-map, height);
  $bg-position-x-px: map-get($icon-map, backgroundX);
  $bg-position-y-px: map-get($icon-map, backgroundY);

  $width-em: mq-px2em($width-px + 1);
  $height-em: mq-px2em($height-px + 1);
  $bg-position-x-em: mq-px2em($bg-position-x-px);
  $bg-position-y-em: mq-px2em($bg-position-y-px);

  @if $type == all {
    @extend %sprite; }

  @if $type == all or $type == size {
    width: $width-em;
    height: $height-em; }

  @if $type == all or $type == position {
    background-position: $bg-position-x-em $bg-position-y-em; }

  .lte-ie9 & {
    @if $type == all {
      @extend %ie-sprite; }

    @if $type == all or $type == size {
      width: $width-px;
      height: $height-px; }

    @if $type == all or $type == position {
      background-position: $bg-position-x-px $bg-position-y-px; } } }
