body {
  min-height: 100vh;
  background: linear-gradient(to bottom right, #ADDF99, #499796); }

.archivement {
  max-width: 640px;
  margin: 0 auto 48px; }

.archivement-inner {
  margin: 8px;
  padding: 24px;
  text-align: center;
  color: #42686A;
  background-color: white;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .1);
  border-radius: 3px; }

.archivement-title {
  font-size: 27px;
  line-height: 1.5;
  font-weight: 500; }

.archivement-description {
  font-size: 18px;
  line-height: 1.5; }

.badge {
  position: relative;
  width: 200px;
  height: 200px;
  margin: 24px auto;

  .badge-shadow {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    border-radius: 50%;
    box-shadow: 0 0 100px #FDEDDE; }

  .badge-ring {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    border-radius: 50%;
    background: linear-gradient(to bottom right, #FBF1E3, #F6C8B0);

    &:before {
      content: '';
      position: absolute;
      top: 0.1%;
      left: 0.1%;
      right: 0.1%;
      bottom: 0.1%;
      border-radius: 50%;
      border: 3px solid #F9E1CB; }

    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      border-radius: 50%;
      border: 3px solid #F9E1CB; } }

  .badge-mask {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    border-radius: 50%;
    background: linear-gradient(to bottom right, rgba(255, 255, 255, .5), rgba(255, 255, 255, 0)); }

  .badge-content {
    position: absolute;
    top: 8%;
    left: 8%;
    right: 8%;
    bottom: 8%;
    border-radius: 50%;
    background-color: white;
    border: 3px solid #D2703D;

    &:after {
      content: '';
      position: absolute;
      top: -3%;
      left: -3%;
      right: -3%;
      bottom: -3%;
      border-radius: 50%;
      border: 3px solid white; } }

  .badge-item-layer1 {
    position: absolute;
    top: 24%;
    left: 24%;
    right: 24%;
    bottom: 24%;
    background-color: #195462;
    clip-path: polygon(74% 8%, 100% 50%, 74% 92%, 26% 92%, 0 50%, 26% 8%); }

  .badge-item-layer2 {
    position: absolute;
    top: 25.5%;
    left: 25.5%;
    right: 25.5%;
    bottom: 25.5%;
    background: linear-gradient(to bottom right, #90D3DE, #20606D);
    clip-path: polygon(74% 8%, 100% 50%, 74% 92%, 26% 92%, 0 50%, 26% 8%); }

  .badge-item-layer3 {
    position: absolute;
    top: 26.5%;
    left: 26.5%;
    right: 26.5%;
    bottom: 26.5%;
    background: linear-gradient(to bottom right, #309AA2, #2E6E85);
    clip-path: polygon(74% 8%, 100% 50%, 74% 92%, 26% 92%, 0 50%, 26% 8%); }

  .badge-item-layer4 {
    position: absolute;
    top: 38%;
    left: 38%;
    right: 38%;
    bottom: 38%;
    border-radius: 50%;
    background: linear-gradient(to bottom right, #90D3DE, #20606D);

    &:after {
      content: '';
      position: absolute;
      top: 3%;
      left: 3%;
      right: 3%;
      bottom: 3%;
      border-radius: 50%;
      background: white;
      border: 3px solid #195462; } }

  &.small {
    .badge-ring {
      &:before {
        border: 1px solid #F9E1CB; }

      &:after {
        border: 1px solid #F9E1CB; } }

    .badge-content:after {
      border: 1px solid white; }

    .badge-item-layer4 {
      &:after {
        border: 1px solid #195462; } } }

  &.lock {
    .badge-shadow, .badge-ring, .badge-content, .badge-mask, .badge-item-layer2, .badge-item-layer3 {
      display: none; }

    .badge-item-layer1 {
      background-color: #EFEFEF; }

    .badge-item-layer4 {
      background: #EFEFEF;
      &:after {
        border: 0; } } } }

.rewards {
  margin-bottom: 24px; }

.reward-title {
  position: relative;
  width: 70%;
  margin: 0 auto 36px;

  span:nth-child(1) {
    position: absolute;
    top: 12px;
    left: -12px;
    width: 48px;
    height: 32px;
    background-color: #E99866;

    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: -16px;
      border: 16px solid transparent;
      border-top-color: #E99866;
      border-bottom-color: #E99866; } }

  span:nth-child(2) {
    position: absolute;
    top: 12px;
    right: -12px;
    width: 48px;
    height: 32px;
    background-color: #E99866;

    &:after {
      content: '';
      position: absolute;
      top: 0;
      right: -16px;
      border: 16px solid transparent;
      border-top-color: #E99866;
      border-bottom-color: #E99866; } }

  span:nth-child(3) {
    display: block;
    position: relative;
    padding: 8px;
    color: white;
    font-size: 13px;
    font-weight: 500;
    letter-spacing: .12em;
    background-color: #F9A26D;

    &:before {
      content: '';
      position: absolute;
      width: 0;
      height: 0;
      top: 100%;
      left: 0;
      border-top: 0 solid transparent;
      border-left: 0 solid transparent;
      border-right: 36px solid #C68157;
      border-bottom: 12px solid transparent; }

    &:after {
      content: '';
      position: absolute;
      width: 0;
      height: 0;
      top: 100%;
      right: 0;
      border-top: 0 solid transparent;
      border-left: 36px solid #C68157;
      border-right: 0 solid transparent;
      border-bottom: 12px solid transparent; } } }


.reward-description {
  font-size: 18px;
  font-weight: 500; }

.level {
  @include pie-clearfix;
  padding: 16px 0 0;
  border-top: 1px solid #F3F5F5;

  .level-item {
    float: left;
    width: calc(100% / 3);

    &.is-active:after {
      content: '';
      display: block;
      margin: 12px auto 0;
      width: 6px;
      height: 6px;
      border-radius: 50%;
      background-color: #2EE7DA; }

    .badge {
      width: 64px;
      height: 64px;
      margin: 8px auto; } } }

.close-button {
  position: fixed;
  bottom: 12px;
  left: calc(50% - 32px);
  width: 64px;
  height: 64px;
  border-radius: 50%;
  background: linear-gradient(to bottom right, #A4F5A9, #6EEECD);
  border: 6px solid #248795; }

.close-button-inner {
  position: relative;
  width: 48px;
  height: 48px;
  margin: 2px;
  border-radius: 50%;
  background-color: #248795;

  &:before {
    content: '';
    position: absolute;
    top: 14px;
    left: 15px;
    width: 26px;
    height: 2px;
    border-radius: 2px;
    background-color: #8AF9C7;
    transform: rotate(45deg);
    transform-origin: top left; }

  &:after {
    content: '';
    position: absolute;
    bottom: 14px;
    left: 15px;
    width: 26px;
    height: 2px;
    border-radius: 2px;
    background-color: #8AF9C7;
    transform: rotate(-45deg);
    transform-origin: bottom left; } }
